import $ from "jquery";

$(function(){
    function backon() {
        $(".b_cover").addClass("back_on");
        $("body").addClass("body_lock");
      };
    
      function backoff() {
        $(".b_cover").removeClass("back_on");
        $("body").removeClass("body_lock");
      };


    $("#h_menu > a").click(function(){
        $(".hm_menu").stop().animate({right:"0"},300);
        backon();
        return false;
    });

    $(".hx_btn").click(function(){
        $(".hm_menu").stop().animate({right:"-100%"},300);
        $(".sm_s").stop().slideUp(300);
        $(".h_smuw > div > a > img").removeClass("open");
        backoff();
        return false;
    });

    $(".h_smuw > div > a").click(function(){
        $(this).children("img").toggleClass("open");
        if($(this).parent("div").next(".sm_s").is(":visible")){
            $(this).parent("div").next(".sm_s").stop().slideUp(300);
        } else {
            $(this).parent("div").next(".sm_s").stop().slideDown(300);
        };
    });

    $(".mb_ms > a").click(function(){
        $(".cal_w").fadeIn(400);
        backon();
        return false;
    });

    $(".cancel").click(function(){
        $(".cal_w").fadeOut(400);
        backoff();
        return false;
    })

    $(".sm_s > li > a").click(function(){
        $(".hm_menu").stop().animate({right:"-100%"},300);
        $(".sm_s").stop().slideUp(300);
        $(".h_smuw > div > a > img").removeClass("open");
        backoff();
    })

    $(".h_smuw > div > a").click(function(){
        return false;
    });

    
    
    $(".ff_i > a").click(function(){
        alert("아직 개발중인 기능입니다.")
    });
});