import { BrowserRouter, Routes, Route } from "react-router-dom";
import './comp/reset.css';
import './comp/App.css';
import './comp/Main.css';
import './hmenu.js';
import Notice from "./comp/Notice.js";
import Faq from "./comp/Faq.js";
import {Header,
        Mmenu,
        Mmain,
        Foot} from "./comp/Main.js";


function App() {
  return (
    <div className="App">
      <div className="b_cover"></div>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Header />
        <Mmenu />
          <Routes>
            <Route exact path="/" element={<Mmain />} />
            <Route path="/Notice" element={<Notice />} />
            <Route path="/Faq" element={<Faq />} />
          </Routes>
        <Foot />
      </BrowserRouter>
    </div>
  );
}

export default App;
